<template>
  <div>
    <FilterContainer />

    <div class="card-container">
      <article class="card-item" v-for="character in characters" :key="character.id">
        <div class="item-image">
          <img :src="character.image" :alt="`Image of ${character.name}`" v-if="character.image">
        </div>
        <div class="item-description" v-if="character">
          <div class="description-text">
            <h2 class="item-name">{{ character.name }}</h2>
            <span class="status" :class="getStatusClass(character.status)">
              <span class="status__icon">{{ character.status }} - </span>
              {{ character.species }}
            </span>
          </div>
          <div class="description-text">
            <span class="text-gray">Last known location: </span>
            <span>{{ character.location ? character.location.name : 'Unknown' }}</span>
          </div>
          <div class="description-text">
            <span class="text-gray">First seen in:</span>
            <span>{{ getFirstSeenEpisodeName(character.episode[0]) }}</span>
          </div>
        </div>
      </article>
    </div>

    <vPagination :info="info" :currentPage="currentPage" @prev="prevPage" @next="nextPage" />
  </div>
</template>

<script>
import { onMounted, computed, watch } from 'vue';
import { useStore } from 'vuex';
import vPagination from './v-pagination.vue';
import FilterContainer from './v-filter.vue';

export default {
  components: {
    vPagination,
    FilterContainer,
  },
  setup() {
    const store = useStore();

    const characters = computed(() => store.getters.filteredCharacters);
    const info = computed(() => store.getters.info);
    const currentPage = computed(() => store.getters.currentPage);
    const episodes = computed(() => store.getters.episodes);

    const fetchCharacters = async (page) => {
      await store.dispatch('fetchCharacters', page);
    };

    const prevPage = () => {
      if (currentPage.value > 1) {
        store.dispatch('setPage', currentPage.value - 1);
      }
    };

    const nextPage = () => {
      if (currentPage.value < info.value.pages) {
        store.dispatch('setPage', currentPage.value + 1);
      }
    };

    const getFirstSeenEpisodeName = (episodeUrl) => {
      const episode = episodes.value.find((ep) => ep.url === episodeUrl);
      return episode ? episode.name : '';
    };

    const getStatusClass = (status) => {
      return {
        'status-alive': status === 'Alive',
        'status-unknown': status === 'unknown',
        'status-dead': status === 'Dead',
      };
    };

    onMounted(() => {
      fetchCharacters(currentPage.value);
    });

    watch(
      () => currentPage.value,
      (newValue) => {
        fetchCharacters(newValue);
      }
    );

    return {
      characters,
      info,
      currentPage,
      prevPage,
      nextPage,
      getFirstSeenEpisodeName,
      getStatusClass,
    };
  },
};
</script>

<style>
.card-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.card-item {
  border: none;
  border-radius: 8px;
  overflow: hidden;
  width: 600px;
  height: 220px;
  display: flex;
  background-color: rgb(60, 62, 68);
  margin: 10px;
}

.item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.item-description {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
  color: #fff;
}

.description-text {
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
}

.status {
  display: flex;
  align-items: center;
}

.status__icon {
  margin-right: 5px;
}

.status__icon::first-letter {
  text-transform: uppercase;
}

.status-alive::before {
  content: '';
  height: 9px;
  width: 9px;
  border-radius: 50%;
  background: rgb(85, 204, 68);
  margin-right: 9px;
  margin-top: 3px;
}

.status-unknown::before {
  content: '';
  height: 9px;
  width: 9px;
  border-radius: 50%;
  background: rgb(158, 158, 158);
  margin-right: 9px;
  margin-top: 3px;
}

.status-dead::before {
  content: '';
  height: 9px;
  width: 9px;
  border-radius: 50%;
  background: rgb(214, 61, 46);
  margin-right: 9px;
  margin-top: 3px;
}

.text-gray {
  font-size: 16px;
  font-weight: 500;
  color: rgb(158, 158, 158);
}
</style>
