<template>
  <div class="filter">
    <input class="filter-input" 
    v-model="name" placeholder="Filter by name" 
    @input="updateFilters">
    <select class="filter-select" 
    v-model="status" 
    @change="updateFilters">
      <option class="filter-select__option" 
      value="">All Statuses</option>
      <option class="filter-select__option" 
      value="alive">Alive</option>
      <option class="filter-select__option" 
      value="dead">Dead</option>
      <option class="filter-select__option"
       value="unknown">Unknown</option>
    </select>
  </div>
</template>

<script>
import { ref } from 'vue';
import { useStore } from 'vuex';

export default {
  setup() {
    const store = useStore();
    const name = ref('');
    const status = ref('');

    const updateFilters = () => {
      store.dispatch('setFilters', { name: name.value, status: status.value });
    };

    return {
      name,
      status,
      updateFilters,
    };
  },
};
</script>

<style>
.filter {
  display: flex;
  gap: 40px;
  justify-content: center;
  margin-bottom: 20px;
}

.filter-input,
.filter-select {
  padding: 10px 20px;
  font-size: 16px;
  max-width: 200px;
}

</style>
